/* SideThreeAndFour.css */

.sidethreeMain, .sidefourMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f2f2f2;
  border-radius: 25px;
}

.sideOne__div1 {
  width: 100%;
  height: 100%;
}

.sideOne__div2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
}

.sideOne__div2 > img {
  height: 80%;
  width: 80%;
  object-fit: cover;
}

.sideOne__container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

