.sideOne__h1 {
  font-size: $font-size-h1;
  margin-bottom: 5rem;
}

.sideOne__h1__dot {
  color: #6699cc;
}

.sideTwo {
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  transition: background-color 0.3s;
}

.sideTwo-container {
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sideTwo__h2 {
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 9rem;
  color: #555555;
  text-align: center;
  text-decoration: underline #6699cc;
  transition: color 0.3s;
  margin-top: 20px;
}

.sideTwo__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 1rem;
}

.sideTwo__div {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #555555;
  font-size: 4rem;
  transition: color 0.3s;
  margin-top: 1rem;
}

.sideTwo__img {
  width: 100%;
  max-width: 800px;
  height: auto;
  border-radius: 25px;
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 1rem;
}

.sideTwo__img:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.sideTwo__animated-img {
  opacity: 0; /* Initially hide the image */
  animation: fadeIn 2s ease-in-out forwards;
}

.sideTwo__text {
  text-align: center;
  color: #555555;
  font-size: 4rem;
  transition: color 0.3s;
  margin-top: 1rem;
}

.sideTwo__text p {
  margin-bottom: 1rem;
}

.sideTwo__text p:last-of-type {
  margin-bottom: 0;
}

.sideTwo__animated-text,
.sideTwo__animated-text--delay,
.sideTwo__animated-text--delay-more {
  display: inline-block;
  letter-spacing: 0.15em;
  opacity: 0; /* Initially hide the text */
  animation: fadeIn 2s ease-in-out forwards;
}

.sideTwo__animated-img {
  animation-delay: 0s; /* Image appears first */
}

.sideTwo__animated-text {
  animation-delay: 2s; /* Text appears after the image */
}

.sideTwo__animated-text--delay {
  animation-delay: 4s; /* Next text block */
}

.sideTwo__animated-text--delay-more {
  animation-delay: 6s; /* Last text block */
}

